import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isMenuVisible: true,
    admin: {
      id: 1,
      name: "Edvan",
    },
    pairs: [
      // {
      //   first: "Edvan",
      //   second: "Thiago",
      //   geoJson: [
      //     {
      //       type: "LineString",
      //       coordinates: [
      //         [-48.11896920204163, -15.836032384556953],
      //         [-48.11799287796021, -15.835402763539472],
      //       ],
      //     },
      //   ],
      // },
      // {
      //   first: "Rafael",
      //   second: "Antonio Pereira",
      //   geoJson: [
      //     {
      //       type: "LineString",
      //       coordinates: [
      //         [-48.11804115772247, -15.834107389536362],
      //         [-48.11769783496857, -15.834520258646338],
      //         [-48.11746180057526, -15.83436543282898],
      //         [-48.11779975891114, -15.833926759035416],
      //         [-48.11716139316559, -15.834731853738177],
      //       ],
      //     },
      //   ],
      // },
    ],
    loadDataInMap: [],
    pairSelect: {},
    configMap: {
      initConfig: false,
      marker: false,
      clearMarker: false,
      coordinate: {
        latitude: [],
        longitude: [],
      },
      loadMapa: false,
    },
  },
  mutations: {
    toggleMenu(state, isVisible) {
      if (isVisible === undefined) state.isMenuVisible = !state.isMenuVisible;
      else state.isMenuVisible = isVisible;
    },
    setPair(state, pair) {
      state.pairs.push(pair);
    },
    initConfigMap(state, init) {
      state.configMap.initConfig = init;
    },
    toggleMarkerMap(state, marker) {
      if (marker == undefined) state.configMap.marker = !state.configMap.marker;
      else state.configMap.marker = marker;
    },
    selectPair(state, pair) {
      state.pairSelect = pair;
    },
    takeLatLong(state, { lat, lng }) {
      if (lat === undefined && lng === undefined) {
        state.configMap.coordinate.latitude = [];
        state.configMap.coordinate.longitude = [];
      } else {
        state.configMap.coordinate.latitude.push(lat);
        state.configMap.coordinate.longitude.push(lng);
      }
    },
    setCoordinatesInPair(state, { indexPair, geoJSON }) {
      state.pairs[indexPair].geoJson.push(geoJSON);
      state.configMap.marker = false;
    },
    clearMarker(state, clear) {
      state.configMap.clearMarker = clear;
    },
    setPairInMap(state, pair) {
      state.loadDataInMap = pair;
    },
  },
  actions: {
    setPairInMap({ state, commit }, indexPair) {
      let data = [];
      let pairs = state.pairs;

      if (indexPair != undefined) {
        let pair = pairs[indexPair];
        data = pairs[indexPair].geoJson.map((item) => {
          return {
            pair: `${pair.first} e ${pair.second}`,
            geoJson: item,
          };
        });
      } else {
        for (let p of pairs) {
          let arrPairs = [];
          arrPairs = p.geoJson.map((item) => {
            return {
              pair: `${p.first} e ${p.second}`,
              geoJson: item,
            };
          });

          data = data.concat(arrPairs);
        }
      }

      commit("setPairInMap", data);
    },
  },
});
