<template>
  <b-modal id="modal-pair" hide-footer size="lg" title="Cadastrar Duplas">
    <b-row>
      <b-col lg="6" md="12">
        <b-form-group
          id="pair-1"
          label="Nome da primeira pessoa (1/2)"
          label-for="name-1">
          <b-form-input v-model="pair.first" id="name-1" trim></b-form-input>
        </b-form-group>
      </b-col>
      <b-col lg="6" md="12">
        <b-form-group
          id="pair-2"
          label="Nome da segunda pessoa (2/2)"
          label-for="name-2">
          <b-form-input v-model="pair.second" id="name-2" trim></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <hr />
    <div class="btn-box">
      <b-button class="mr-2" @click="$bvModal.hide('modal-pair')">
        Cancelar
      </b-button>
      <b-button variant="primary" @click="setPair">Salvar</b-button>
    </div>
  </b-modal>
</template>

<script>
export default {
  data() {
    return {
      pair: {},
    };
  },
  methods: {
    setPair() {
      if (
        [undefined, ""].includes(this.pair.first) ||
        [undefined, ""].includes(this.pair.second)
      ) {
        alert("Um dos campos não estão preenchidos");
        return;
      }

      this.pair.geoJson = [];

      this.$store.commit("setPair", this.pair);
      this.pair = {};
      this.$bvModal.hide("modal-pair");
    },
  },
};
</script>

<style scoped>
.btn-box {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
</style>
