<template>
  <div class="accordion" role="tablist">
    <a v-b-toggle="'accordion-' + index" @click="resetConfig">
      <i class="fa fa-arrow-right mr-2"></i>
      {{ pair.first }} e {{ pair.second }}
    </a>
    <b-collapse
      :id="'accordion-' + index"
      accordion="my-accordion"
      class="accordion-body"
      role="tabpanel">
      <div class="box-button">
        <b-button variant="primary" @click="addStreet">Adicionar Rua</b-button>
        <b-button variant="primary" :disabled="disableButtonSave" @click="save">
          Salvar</b-button
        >
      </div>
      <b-button variant="success">Concluir Rua</b-button>
      <b-button variant="warning" @click="loadStreetMap">
        Carregar Ruas
      </b-button>
    </b-collapse>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "CollapseMenu",
  props: {
    pair: Object,
    index: Number,
  },
  computed: {
    ...mapState(["pairSelect", "configMap"]),
    disableButtonSave() {
      return this.configMap.coordinate.latitude.length < 2;
    },
  },
  methods: {
    ...mapActions(["setPairInMap"]),
    resetConfig() {
      this.$store.commit("initConfigMap", false);
      this.$store.commit("toggleMarkerMap", false);
      this.$store.commit("takeLatLong", {});
      this.$store.commit("clearMarker", true);
    },
    addStreet() {
      this.$store.commit("initConfigMap", true);
      this.$store.commit("toggleMarkerMap", false);
      this.$store.commit("selectPair", this.pair);
      this.$store.commit("clearMarker", false);
    },
    save() {
      let arrayMap = this.configMap.coordinate.latitude.map((item, index) => {
        return [this.configMap.coordinate.longitude[index], item];
      });

      let geoJSON = {
        type: "LineString",
        coordinates: arrayMap,
      };

      const indexPair = this.index;

      this.$store.commit("setCoordinatesInPair", { indexPair, geoJSON });
      this.$store.commit("clearMarker", true);
      this.$store.commit("takeLatLong", {});
    },
    loadStreetMap() {
      this.setPairInMap(this.index);
    },
  },
};
</script>

<style lang="scss" scoped>
a {
  text-decoration: none;
  color: inherit;
  width: 100%;
  cursor: pointer;

  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.accordion-body {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  button {
    margin: 4px 0;
  }

  .box-button {
    display: flex;

    button:first-child {
      flex: 1;
      margin-right: 8px;
    }
  }
}
</style>
