<template>
  <div class="mapa">
    <b-dropdown
      v-if="configMap.initConfig"
      id="dropdown-1"
      text="Marcadores"
      class="m-md-2 float-right">
      <b-dropdown-item @click="initMarcador">
        Adicionar Marcadores
      </b-dropdown-item>
      <b-dropdown-item @click="removeMarcador">
        Remover Marcadores
      </b-dropdown-item>
    </b-dropdown>

    <!-- <b-card> -->
    <!-- {{ pairSelect }} -->
    <!-- {{ configMap }} -->
    <!-- </b-card> -->

    <div @click="acaoMapa" id="mapid" style="height: 500px; width: 100%">
      <ButtonComponent :variable="mapaStreetValue" topBase="">
        <div class="block-btn" @click="mapaStreetValue = !mapaStreetValue">
          <img width="25" src="@/assets/img/mapa-modo.png" />
        </div>
      </ButtonComponent>

      <ButtonComponent :variable="configMap.marker" topBase="2">
        <div class="block-btn" @click="$store.commit('toggleMarkerMap', false)">
          <img width="25" src="@/assets/img/alfinete.png" />
        </div>
      </ButtonComponent>
    </div>
  </div>
</template>

<script>
import ButtonComponent from "@/components/pages/map/ButtonComponent.vue";
import { mapState } from "vuex";

export default {
  components: { ButtonComponent },
  computed: {
    ...mapState(["configMap", "pairSelect", "loadDataInMap"]),
    clearMarkers() {
      return this.configMap.clearMarker;
    },
  },
  data() {
    return {
      mymap: null,
      layer: null,
      markers: {},
      layerMarcacao: null,
      mapaStreetValue: false,
      latitude: [],
      longitude: [],
      seenPlaces: [
        {
          name: "Edvan e Thiago",
          geoJSON: {
            type: "LineString",
            coordinates: [
              [-48.11903357505799, -15.836063349474399],
              [-48.120428323745735, -15.83437059369147],
            ],
          },
        },
      ],
      showModal: false,
    };
  },
  mounted() {
    this.gerarMapa();
    // eslint-disable-next-line
    this.layer = L.geoJSON().addTo(this.mymap);
    // eslint-disable-next-line
    this.layerMarcacao = L.geoJSON().addTo(this.mymap);

    // this.loadMapa();
  },
  watch: {
    mapaStreetValue() {
      this.mapaStreet();
    },
    clearMarkers(value) {
      if (value) this.removeMarcador();
    },
    loadDataInMap() {
      this.loadMapa();
    },
  },
  methods: {
    gerarMapa() {
      // eslint-disable-next-line
      this.mymap = L.map("mapid").setView(
        ["-15.836063349474399", "-48.11903357505799"],
        16.5
      );
      // eslint-disable-next-line
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        maxZoom: 18,
        attribution:
          'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, ' +
          '<a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, ',
        id: "mapbox.streets",
        fadeAnimation: true,
      }).addTo(this.mymap);
    },
    mapaStreet() {
      if (this.mapaStreetValue) {
        // eslint-disable-next-line
        L.tileLayer("http://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}", {
          maxZoom: 20,
          subdomains: ["mt0", "mt1", "mt2", "mt3"],
        }).addTo(this.mymap);
      } else {
        // eslint-disable-next-line
        L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
          maxZoom: 20,
          attribution:
            'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, ' +
            '<a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, ',
          id: "mapbox.streets",
        }).addTo(this.mymap);
      }
    },
    acaoMapa() {
      let vm = this;
      this.mymap.on("click", function (e) {
        if (vm.configMap.marker) {
          // console.log("depois do marcador está com true - dentro do map");
          vm.addMarcador(e.latlng.lat, e.latlng.lng);
        }
      });
    },
    addMarcador(lat, lng) {
      // eslint-disable-next-line
      this.markers = L.marker([lat, lng])
        .addTo(this.layerMarcacao)
        .bindPopup(`teste`);

      if (
        lat !=
          this.configMap.coordinate.latitude[
            this.configMap.coordinate.latitude.length - 1
          ] &&
        lng !=
          this.configMap.coordinate.longitude[
            this.configMap.coordinate.longitude.length - 1
          ]
      ) {
        this.$store.commit("takeLatLong", { lat, lng });
      }
    },
    initMarcador() {
      if (this.clearMarkers) {
        this.removeMarcador();
      }
      this.$store.commit("toggleMarkerMap", true);
      this.$store.commit("clearMarker", false);
    },
    removeMarcador() {
      this.mymap.removeLayer(this.layerMarcacao);
      // eslint-disable-next-line
      this.layerMarcacao = L.geoJSON().addTo(this.mymap);
    },
    setMap(coord, concat, color, layer) {
      var array = {
        type: "FeatureCollection",
        features: [
          {
            type: "Feature",
            geometry: coord,
            properties: {
              OBJECTID: concat,
              // "OBJECTID": JSON.stringify(objectid),
            },
          },
        ],
      };
      // eslint-disable-next-line
      L.geoJson(array, {
        style: function () {
          return {
            stroke: true,
            color: color,
            weight: 5,
          };
        },
        onEachFeature: function (feature, l) {
          l.bindPopup(feature.properties.OBJECTID);
          l.on("click", function () {
            // vm.showModal = true;
            l.setStyle({
              weight: 12,
              outline: "red",
            });
          });
          l.on("popupclose", function () {
            l.setStyle({
              weight: 5,
            });
          });
        },
      }).addTo(layer);
    },
    loadMapa() {
      this.layer.clearLayers();

      this.loadDataInMap.forEach((item) => {
        let concat = "Duplas de Anúncio <br>";
        concat += `Nome: ${item.pair}<br>`;
        // concat += `<button type="button" onclick=''>Finalizar rua</button><br>`;

        this.setMap(item.geoJson, concat, "#ff0000", this.layer);
        this.mymap.setView(this.layer.getBounds().getCenter());
        this.mymap.fitBounds(this.layer.getBounds());
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#mapid {
  border: 1px solid black;
  box-shadow: 0 1px 5px rgba(255, 255, 255, 0.2);
}

.baseMapa {
  position: absolute !important;
  right: 10px !important;
  text-align: center !important;
  z-index: 900 !important;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid rgb(177, 175, 175);

  cursor: pointer;

  &.btn-map-active {
    background-color: darkgrey;
  }

  &.topBaseMapa {
    top: 15px;
  }

  &.topBaseMapa2 {
    top: 60px;
  }

  .block-btn {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
  }
}
</style>
