<template>
  <b-modal id="modal-signin" hide-footer size="md" title="Entrar">
    <b-form-group id="email" label="Email" label-for="email-user">
      <b-form-input
        id="email-user"
        type="email"
        v-model="user.email"
        trim></b-form-input>
    </b-form-group>
    <b-form-group id="password" label="Senha" label-for="password-user">
      <b-form-input
        id="password-user"
        type="password"
        v-model="user.password"
        trim></b-form-input>
    </b-form-group>
    <hr />
    <div class="btn-box">
      <b-button class="mr-2" @click="$bvModal.hide('modal-signin')">
        Cancelar
      </b-button>
      <b-button variant="primary" @click="signin">Entrar</b-button>
    </div>
  </b-modal>
</template>

<script>
export default {
  data() {
    return {
      user: {},
    };
  },
  methods: {
    signin() {
      if (
        [undefined, ""].includes(this.user.email) ||
        [undefined, ""].includes(this.user.password)
      ) {
        alert("Um dos campos não estão preenchidos");
        return;
      }
    },
  },
};
</script>

<style scoped>
.btn-box {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
</style>
