<template>
  <ul class="menu">
    <li>
      <a href @click.prevent="setPairInMap()">
        <i class="fa fa-arrow-right mr-2"></i>
        Todas as Duplas
      </a>
    </li>
    <li v-for="(i, index) in pairs" :key="index">
      <a v-if="!admin.id" href @click.prevent="this.setPairInMap(index)">
        <i class="fa fa-arrow-right mr-2"></i>
        {{ i.first }} e {{ i.second }}
      </a>

      <Collapse :pair="i" :index="index" />
    </li>
  </ul>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Collapse from "./Collapse.vue";

export default {
  name: "MenuPage",
  components: { Collapse },
  computed: mapState(["admin", "pairs"]),
  methods: {
    ...mapActions(["setPairInMap"]),
  },
};
</script>

<style lang="scss" scoped>
.menu {
  padding: 0;
  list-style-type: none;

  li {
    background-color: rgba($color: #fff, $alpha: 0.5);
    padding: 5px 8px 5px 16px;
    margin-bottom: 8px;
    border-radius: 8px;

    a {
      text-decoration: none;
      color: inherit;
      width: 100%;
      cursor: pointer;

      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
  }
}
</style>
